  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
  body--no-scrollbar {
    height: 100vh;
    -ms-overflow-style: none;
    scrollbar-width: none; }
    body--no-scrollbar::-webkit-scrollbar {
      display: none; }
  body .header,
  body .footer {
    flex-shrink: 0; }
  body main {
    flex-grow: 1; }

.scroll-lock {
