.btn-reset {
  padding: 0;
  background: transparent;
  border: none; }

.btn-wrapper {
  display: flex;
  width: 100%;
  margin-top: 20px; }
  .btn-wrapper--center {
    justify-content: center; }

.nowrap {
  white-space: nowrap; }

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1358px;
  margin: 0 auto;
  padding: 0 40px; }
  .container--external {
    max-width: none;
    padding: 0 70px; }
    @media (max-width: 1417px) {
      .container--external {
        max-width: 1358px;
        padding: 0 40px; } }
  @media (max-width: 1139px) {
    .container {
      max-width: 1342px;
      padding: 0 32px; } }
  @media (max-width: 959px) {
    .container {
      padding: 0 20px; } }
  @media (max-width: 719px) {
    .container--no-padding {
      padding: 0; } }

.table {
  border-collapse: collapse; }
  .table th {
    padding: 3px 20px 4px 10px;
    border: 1px solid #e5e5e5; }
    .table th:first-child {
      border-left: none; }
    .table th:last-child {
      border-right: none; }
    .table th label {
      font-size: 12px;
      line-height: 20px;
      text-transform: uppercase; }
      .table th label:hover {
        cursor: pointer; }
      .table th label input {
        vertical-align: middle;
        margin: 0; }
        .table th label input:hover {
          cursor: pointer; }
      .table th label span {
        vertical-align: middle; }
  .table td {
    padding: 16px 20px 12px 10px;
    border-bottom: 1px solid #e5e5e5; }

.table__color-example {
  display: inline-block;
  width: 15px;
  height: 15px;
  vertical-align: middle; }

.table__color-text {
  display: inline-block;
  vertical-align: middle; }

.color-section {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-color: #ffffff; }
  .color-section h1 {
    padding: 0 22px;
    margin: 17px 0 0;
    font-size: 24px;
    line-height: 36px; }

.color-section__btn-reset {
  display: flex;
  align-items: center;
  margin-top: 12px;
  margin-left: auto;
  padding: 10px 25px; }
  .color-section__btn-reset:not([disabled]):hover {
    cursor: pointer; }
  .color-section__btn-reset span {
    margin-left: 5px;
    font-size: 14px;
    color: #0075ff; }
  .color-section__btn-reset[disabled] span {
    color: gray; }
  .color-section__btn-reset svg {
    width: 20px;
    height: 20px;
    fill: #0075ff; }
  .color-section__btn-reset[disabled] svg {
    fill: gray; }

.color-section__table {
  width: 100%;
  margin-top: 10px; }

